import { getClassName } from "@miq/utils";

import "./views.scss";

/**
 * BASE LAYOUT
 * @param {*} props
 * @returns
 */

const ViewHeader = (props) => {
    const { header, title, actions } = props;

    if (!header) return null;

    return (
        <header className="miq-view-header">
            <div className="miq-view-header-inner">
                <div className="miq-view-title">{title && <h2>{title}</h2>}</div>

                {actions && <section className="miq-view-actions">{actions}</section>}
            </div>
        </header>
    );
};

export const AdminView = ({ children, ...props }) => {
    const { title, actions, ...rest } = props;
    const header = title || actions;
    return (
        <div {...rest} className={getClassName(["miq-view", props.className])}>
            <ViewHeader {...{ title, actions, header }} />

            {children}
        </div>
    );
};
