import { useContext } from "react";
import PropTypes from "prop-types";

import "./layout.scss";

import { IconNavLink, Loading } from "@miq/components";
import { SharedDataCtx } from "@miq/contexts";
import { getClassName } from "@miq/utils";
import { hasPerms } from "../utils";

export const AdminLayout = ({ children, ...props }) => {
    const ctx = useContext(SharedDataCtx);

    if (!ctx) return null;
    if (!ctx.isLoaded) return <Loading />;

    const {
        user,
        // perms
    } = ctx;

    if (!user || !user.is_staff) return <div>Not allowed ...</div>;

    return (
        <div id={props.id} className={getClassName(["miq-admin", props.className])}>
            {children}
        </div>
    );
};

AdminLayout.propTypes = {
    match: PropTypes.object.isRequired,
};

/**
 * MAIN
 * @param {*} param0
 * @returns
 */

export const AdminMain = ({ children, ...props }) => {
    return (
        <main {...props} className={getClassName(["miq-admin-main", props.className])} role="main">
            <section className="container">{children}</section>
        </main>
    );
};

/**
 * SIDEBAR
 */

export const AdminNavLink = ({ requiredPerms, ...props }) => {
    const ctx = useContext(SharedDataCtx);
    const { user, perms } = ctx;

    if (!user || !user.is_staff || (requiredPerms && !hasPerms(perms.perms, requiredPerms))) return null;

    return <IconNavLink {...props} className={getClassName(["miq-admin-aside-link", props.className])} />;
};
AdminNavLink.propTypes = {
    requiredPerms: PropTypes.arrayOf(PropTypes.string),
};

export const AdminSidebar = ({ children, header, footer, ...props }) => {
    return (
        <nav {...props} className={getClassName(["miq-admin-aside", props.className])}>
            {header && <header className="miq-admin-aside-header">{header}</header>}

            <section className="miq-admin-aside-body">
                <div className="miq-admin-aside-body-content">{children}</div>

                {footer && <div className="miq-admin-aside-body-footer">{footer}</div>}
            </section>
        </nav>
    );
};

AdminSidebar.NavLink = AdminNavLink;

/**
 * MOBILE NAVIGATION
 */

export const AdminMobileNavbar = ({ children, ...props }) => {
    return (
        <nav {...props} className={getClassName(["miq-admin-mobile-nav", props.className])}>
            {children}
        </nav>
    );
};

/**
 * EXPORTS
 */

export * from "./components";
