import { lazy, Suspense } from "react";
import PropTypes from "prop-types";
import { Route, Switch } from "react-router-dom";

import { Loading } from "@miq/components";

import "./main.scss";

const PageUpdateView = lazy(() => import("./PageUpdateView/"));
const PageListView = lazy(() => import("./PageListView"));

const propTypes = {
    match: PropTypes.object.isRequired,
};

export const PagesLayout = (props) => {
    return (
        <Suspense fallback={<Loading />}>
            <Switch>
                <Route path={`${props.match.path}:pageSlug/`} component={PageUpdateView} />
                <Route path={props.match.path} component={PageListView} />
            </Switch>
        </Suspense>
    );
};

PagesLayout.propTypes = propTypes;
